:root[data-theme="light"] {
  --deep-black: #111;
  --soft-black: #202024;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --backdrop-color: #1116;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 2px;
  --theme-switcher-gradient: linear-gradient(#4f2ee8, #fff);
}

:root[data-theme="dark"] {
  --deep-black: #fff;
  --soft-black: #202024;
  --purple: #eac645;
  --yellow: #4f2ee8;
  --white: #202024;
  --backdrop-color: #1116;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 17px;
  --theme-switcher-gradient: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
}

:root {
  --regular: font-weight: 400;
  --medium: font-weight: 500;
  --bold: font-weight: 700;
  --function-duration: .35s;
  --function-timing: cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  border: 2px solid var(--deep-black);
  z-index: 3;
  background-color: var(--white);
  border-radius: 0 0 8px 8px;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 24px;
  margin: 0 auto;
  padding: 18px 20px;
  display: flex;
  position: relative;
  top: 0;
}

.user-photo-img {
  border-radius: 50%;
  width: 37px;
  height: 37px;
  margin-right: 12px;
}

.logo {
  background: var(--icon-logo);
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: 109px;
  height: 28px;
  margin-right: auto;
  font-weight: 600;
  display: flex;
}

.header-title {
  font-family: DMSans-400, sans-serif;
}

.header-wrap {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.theme-switcher {
  background-image: var(--theme-switcher-gradient);
  cursor: pointer;
  border-radius: 50px;
  align-items: center;
  width: 39px;
  height: 22px;
  margin-right: 14px;
  padding: 1px 0 1px 1px;
  display: flex;
  position: relative;
}

.theme-switcher:before {
  content: "";
  transform: translateX(var(--switch-circle-pos));
  background-color: #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: transform .25s;
  position: absolute;
  box-shadow: 1px 1px 2px #0000001a;
}

.mob-menu-open, .mob-menu-close {
  stroke: var(--deep-black);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
}

.sign-up-btn, .header-nav {
  display: none;
}

.username {
